body,
html {
	height: 100%;
	margin: 0;
	padding: 0;
	font-family: 'Abril Fatface, cursive, Helvetica, sans-serif';
	background-image: url('../../../bgImage/background-image.png');
	background-size: cover;
	background-attachment: fixed;
}

h1 {
	font-size: 24px;
	margin: 10px 0 0 0;
	font-weight: lighter;
	text-transform: uppercase;
	color:rebeccapurple;
}

p {
	font-size: 15px;
	font-weight: normal;
	color:wheat;
}

span a {
    justify-content: center;
	font-size: 18px;
	color: #cccccc;
	text-decoration: none;
	margin: 0 10px;
}

@keyframes float {
	0% {
		box-shadow: 0 5px 15px 0px rgba(0,0,0,0.6);
		transform: translatey(0px);
	}
	50% {
		box-shadow: 0 25px 15px 0px rgba(0,0,0,0.2);
		transform: translatey(-20px);
	}
	100% {
		box-shadow: 0 5px 15px 0px rgba(0,0,0,0.6);
		transform: translatey(0px);
	}
}

.container {
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: column;
	justify-content: center;
    align-items: center;
	padding-top: 100px;
}

.avatar {
	width: 150px;
	height: 150px;
	box-sizing: border-box;
	border: 5px white solid;
	border-radius: 50%;
	overflow: hidden;
	box-shadow: 0 5px 15px 0px rgba(0,0,0,0.6);
	transform: translatey(0px);
	animation: float 6s ease-in-out infinite;
    width: auto; 
    height: auto;
}

.content {
	width: 100%;
	max-width: 600px;
	padding: 20px 40px;
	box-sizing: border-box;
	text-align: center;
}


